<template>
  <div class="login">
    <el-row class="hidden-md-and-up">
      <el-col>
        <div class="sm_bg">
          <div class="welcome_text">
            <p>Welcome</p>
            <p>to GoNowEx</p>
            <span class="el-icon-bottom" style="margin-top: 15px"></span>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex">
      <el-col :md="8" :xs="24" :sm="24">
        <div class="form_box">
          <div class="logo">
            <Logo :size="80"/>
          </div>
          <p class="welcome">
            <span>Welcome to <span class="text">{{webLUText}}</span></span>
          </p>
          <p class="tips">
            Before you get started, you must login or register if you don't already have an account.
          </p>
          <el-form label-position="top" :rules="rules" label-width="80px" class="form" ref="loginForm" :model="loginForm">
            <el-form-item label="Email" prop="email">
              <el-input v-model="loginForm.email"></el-input>
            </el-form-item>
            <el-form-item label="Password" prop="password">
              <el-input type="password" v-model="loginForm.password"></el-input>
            </el-form-item>
            <p class="remember">
              <el-checkbox v-model="checked">Remember Me</el-checkbox>
            </p>
          </el-form>
          <div class="text_btn">
            <span class="A_Text" @click="$router.push('/forgotpassword')">Forgot Password?</span>
            <el-button type="primary" :loading="loading" @keyup.enter="handleSubmit('loginForm')" @click="handleSubmit('loginForm')" size="nomal">Login</el-button>
          </div>
          <div class="create">
            Don't have an account? <span class="A_Text" @click="$router.push('/register')">Create new one</span>
          </div>
          <Footer />
        </div>
      </el-col>
      <el-col :md="16" class="login_bg hidden-md-and-down">
        <div class="welcome_text">
          <p>Welcome</p>
          <p>to {{webLUText}}</p>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { webLUText } from '@/tool/commonText'
import { mapActions } from 'vuex'
import md5 from 'js-md5'
import { login } from '@/api'
export default {
  data () {
    return {
      webLUText,
      loginForm: {},
      checked: false,
      loading: false,
      rules: {
        email: [
          { required: true, message: '请输入Email', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入Password', trigger: 'blur' },
        ]
      }
    }
  },
  mounted () {
    this.handleSetPwd()
  },
  methods: {
    handleSetPwd () {
      const form = this.$storage.get_ls('RM')
      if (form) {
        const { email, password } = form
        this.loginForm = {
          email,
          password
        }
      }
    },
    handleSavePwd () {
      if (this.checked) {
        const { email, password } = this.loginForm
        this.$storage.set_ls('RM', { email, password, time: +new Date })
      } else {
        this.$storage.rm_ls('RM')
      }
    },
    handleSubmit (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const { email: username, password } = this.loginForm
          const pwd = md5(password)
          this.loading = true
          const [res] = await login({ username, pwd })
          this.loading = false
          if (res) {
            this.handleSavePwd()
            const { data } = res
            this.LOGIN(data)
            this.$message('登录成功')
            this.$router.push('/')
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      })
    },
    ...mapActions(['LOGIN'])
  }
}
</script>
<style lang="less" scoped>
@import url('../../assets/css/var.less');
@bg_url: url('https://www.2008php.com/2019_Website_appreciate/2019-04-01/20190401161810AqM5QAqM5Q.jpg');
.login {
  .bg {
    position: relative;
    background-image: @bg_url;
    background-repeat: no-repeat;
  }
  .sm_bg {
    .bg;
    background-size: cover;
    min-height: 95vh;
  }
  .login_bg {
    .bg;
    min-height: 100vh;
    background-size: 100%;
    background-position: 0 100%;
    animation-name: bg_img;
    animation-duration: 80s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
  .form_box {
    padding: 40px 45px;
    .logo {
      display: inline-block;
    }
    .welcome {
      font-size: 22px;
      padding: 50px 0 20px;
      .text {
        font-weight: bold;
      }
    }
    .tips {
      color: #98a6ad;
      font-weight: 300;
      line-height: 30px;
      font-size: 14px;
    }
    .form {
      margin-top: 20px;
    }
  }
  .text_btn {
    display: flex;
    justify-content: space-between;
    line-height: 45px;
    margin: 20px 0;
  }
  .remember {
    margin-top: 40px;
  }
  .create {
    margin: 40px 0;
    font-size: 14px;
    text-align: center;
    color: @tips_color;
  }
  .welcome_text {
    color: #fff;
    position: absolute;
    bottom: 35px;
    left: 50px;
    > p {
      &:first-child {
        font-size: 40px;
        font-weight: bold;
      }
      &:last-child {
        line-height: 50px;
      }
    }
  }
  @keyframes bg_img {
    0% {
      background-position: 0 0%;
    }
    50% {
      background-position: 0 100%;
    }
    100% {
      background-position: 0 0%;
    }
  }
}
</style>